body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #ffffff;
  color: #333333;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header, .footer {
  background-color: #e6f0fa;
  padding: 1rem;
  text-align: center;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-img {
  max-width: 80px;
  height: auto;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}

.nav a {
  text-decoration: none;
  font-weight: bold;
  color: #003366;
}

.nav select {
  padding: 0.4rem;
}

.page {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  max-width: 1920px;
  margin: 0 auto;
  box-sizing: border-box;
}

aside.banner, aside.tags {
  flex: 1;
  background-color: #f0f8ff;
  padding: 1rem;
  border-radius: 10px;
  min-width: 180px;
  box-sizing: border-box;
}

main.content {
  flex: 3;
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 10px;
  box-sizing: border-box;
}

.page h2, .page h3 {
  margin-top: 0;
  margin-bottom: 1rem;
}

.page p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.banner-img, .ad-img, .logo-img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
  display: block;
  margin: 10px auto;
  max-width: 100%;
}

footer {
  background-color: #dce9f9;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
  font-size: 0.9rem;
}

/* Медиа-запросы */
@media (max-width: 1440px) {
  .nav {
    gap: 0.5rem;
  }
}

@media (max-width: 1280px) {
  .page {
    flex-direction: column;
    align-items: stretch;
  }
  aside.banner, aside.tags, main.content {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: center;
  }

  .nav a, .nav select {
    margin: 0.5rem 0;
  }

  .page {
    padding: 0.5rem;
  }

  .logo-img {
    max-width: 60px;
  }

  main.content {
    padding: 1rem;
  }

  .page p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .page {
    padding: 0.25rem;
  }

  aside.banner, aside.tags, main.content {
    padding: 0.5rem;
  }

  .banner-img, .ad-img {
    border-radius: 6px;
  }

  .page h2 {
    font-size: 1.4rem;
  }

  .page p {
    font-size: 0.95rem;
  }
}
